.intro_programme {
  margin: 130px auto 50px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  img {
    width: 300px;
    height: 420px;
    margin: auto;
  }

  h1 {
    text-align: center;
    color: $title-color;
  }
  p {
    width: 90%;
    margin: 30px auto;
    text-align: justify;
    text-align: center;
  }
  a {
    display: inline-block;
    transform: translateX(7px);
    color: $main-color;
    font-weight: 600;
    font-family: $title-font-family;

    &:hover {
      color: $title-color;
    }
  }
}

@media screen and (min-width: 800px) {
  .intro_programme {
    width: 80%;
    img {
      width: 500px;
      height: 700px;
      margin: auto;
    }

    p {
      width: 80%;
    }
  }
}
